*, *:after, *:before { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }
.clearfix:before, .clearfix:after { display: table; content: ''; }
.clearfix:after { clear: both; }


body {
	background: #fff;
	color: #69606b;
	font-weight: 400;
	font-size: 22px;
	line-height: 1.5;
	font-family: 'Inter', Arial, sans-serif;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}

.demo-7 {
	background: #f5f5f5;
}

.demo-5 {
	background: #f8dab4;
	color: #5c554c;
}

.demo-6 {
	background: #f1f1f1;
}

.demo-1 {
	background: #43434b;
}

a {
	outline: none;
	color: #cf4a5c;
	text-decoration: none;
}

a:hover, a:focus {
	color: #69606b;
}

.demo-4 a {
	color: #43939d;
}

.demo-4 a:hover,
.demo-4 a:focus {
	color: #7b8d92;
}

.demo-8 a {
	color: #cf7000;
}

.demo-8 a:hover,
.demo-8 a:focus {
	color: #cf7000;
}

/* To Navigation Style */
.codrops-top {
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	text-transform: uppercase;
	font-weight: 800;
	font-size: 0.6em;
	letter-spacing: 1px;
	z-index: 10000;
}

.codrops-top a {
	display: inline-block;
    color: #fff !important;
    font-size: 10px;
    text-decoration: none;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
}

.codrops-top span.right {
	float: right;
}

.codrops-top span.right a {
	display: block;
	float: left;
}

.codrops-icon:before {
	text-transform: none;
	font-weight: normal;
	font-style: normal;
	font-variant: normal;
	font-family: 'codropsicons';
	text-align: center;
	speak: none;
	margin: 0 0.55em 0 0;
	-webkit-font-smoothing: antialiased;
}

.codrops-icon-drop:before {
	content: "\e001";
}

.codrops-icon-prev:before {
	content: "\e004";
}

.codrops-demos {
	padding: 0 0 1em;
	position: relative;
}

.codrops-demos a {
	font-weight: 800;
	text-transform: uppercase;
	font-size: 0.6em;
	letter-spacing: 1px;
	padding: 0.3em 0;
	display: inline-block;
	opacity: 0.7;
	margin: 0 0.5em;
	white-space: nowrap;
	color: #cf4a5c;
}

.codrops-demos a:hover,
.codrops-demos a.current-demo {
	opacity: 1;
	color: #69606b;
}

.codrops-demos a.current-demo {
	border-bottom: 3px solid #69606b;
}

.related {
	font-size: 6px;
    line-height: 10px !important;
    width: 90%;
    text-transform: uppercase;
    font-family: Inter;
    text-align: center;
    line-height: 0px;
    color: #69606b;
    width: 100%;
    opacity: 0.4 !important;
    opacity: 0.8;
    padding-top: 0px;
    font-weight: 600;
    border-top: 1px solid rgba(105,96,107,0.1);
    position: relative;
    letter-spacing: 0.4px;
    margin-bottom: 30px;
    top: 30px;
    line-height: 12px;
    .icons-intro{
    	bottom: 50px;
    }
}

.demo-5 .related {
	width: 60%;
	margin-left: 40%;
}

@media screen and (max-width: 40em) {

	.codrops-icon span {
		display: none;
	}

	.related {
		font-size: 80%;
	}
}